<template>
  <div class="chart">
    <el-carousel :interval="5000" :height="heightValue">
      <el-carousel-item>
        <el-card class="card" :style="{height: heightValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title small">
              <span>不良品细节 - 匀浆</span>
            </div>
          </div>
          <div class="card-body">
            <ve-waterfall :height="'380px'" name="chart" :data="coatingData" :settings="coatingSettings"></ve-waterfall>
          </div>
        </el-card>
      </el-carousel-item>
      <el-carousel-item>
        <el-card class="card" :style="{height: heightValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title small">
              <span>不良品细节 - 涂布</span>
            </div>
          </div>
          <div class="card-body">
            <ve-waterfall :height="'380px'" name="chart" :data="stirData" :settings="stirSettings"></ve-waterfall>
          </div>
        </el-card>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'positiveReject',
  data () {
    return {
      coatingData: {
        columns: ['不良品名称', '不良品数量'],
        rows: [
          { '不良品名称': '胶水粘度', '不良品数量': 100 },
          { '不良品名称': '胶水固含量', '不良品数量': 60 },
          { '不良品名称': '出料外观', '不良品数量': 54 }
        ]
      },
      coatingSettings: {
        dimension: '不良品名称',
        metrics: '不良品数量'
      },
      stirData: {
        columns: ['不良品名称', '不良品数量'],
        rows: [
          { '不良品名称': '外观', '不良品数量': 50 },
          { '不良品名称': '尺寸', '不良品数量': 56 },
          { '不良品名称': '面密度', '不良品数量': 125 },
          { '不良品名称': '粘附性', '不良品数量': 75 },
          { '不良品名称': '厚度', '不良品数量': 26 }
        ]
      },
      stirSettings: {
        dimension: '不良品名称',
        metrics: '不良品数量'
      }
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'mainArticleItemVerticalPadding'
    ]),
    heightValue: function () {
      return (this.mainHeight / 2) - (this.mainArticleVerticalPadding) - (this.mainArticleItemVerticalPadding) + 10 + 'px'
    }
  },
  methods: {
  },
  mounted: function () {
    // this.$nextTick(
    //   () => {
    //     this.$refs.chart.resize()
    //   }
    // )
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.card {
  overflow: hidden;
}
</style>


