import {
  SAVE_WINDOW_HEIGHT,
  SAVE_MAIN_ARTICLE_HEIGHT,
  SAVE_MAIN_HEIGHT,
  SAVE_MAIN_ARTICLE_VERTICAL_PADDING,
  SAVE_ARTICLE_ITEM_VERTICAL_PADDING
} from './mutation-types.js'

export default {
  [SAVE_WINDOW_HEIGHT] (state, { height }) {
    state.windowHeight = height
  },
  [SAVE_MAIN_HEIGHT] (state, { height }) {
    state.mainHeight = height
  },
  [SAVE_MAIN_ARTICLE_HEIGHT] (state, { height }) {
    state.mainArticleHeight = height
  },
  [SAVE_MAIN_ARTICLE_VERTICAL_PADDING] (state, { verticalPadding }) {
    state.mainArticleVerticalPadding = verticalPadding
  },
  [SAVE_ARTICLE_ITEM_VERTICAL_PADDING] (state, { verticalPadding }) {
    state.mainArticleItemVerticalPadding = verticalPadding
  }
}



