<template>
  <div class="chart">
    <el-carousel :interval="5000" :height="heightValue">
      <el-carousel-item>
        <el-card class="card" :style="{height: heightValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title small">
              <span>极片总体产量</span>
            </div>
          </div>
          <div class="card-body">
            <ve-histogram :height="'380px'" name="chart" :data="chartData" :settings="chartSettings"></ve-histogram>
          </div>
        </el-card>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'positiveProduction',
  data () {
    return {
      chartData: {
        columns: ['日期', '生产量', '良品数量', '良品率', '不良品数量', '不良品率'],
        rows: [
          { '日期': '10月30日', '生产量': 1500, '良品数量': 1400, '良品率': 0.93, '不良品数量': 150, '不良品率': 0.01 },
          { '日期': '10月31日', '生产量': 1450, '良品数量': 1350, '良品率': 0.93, '不良品数量': 100, '不良品率': 0.07 },
          { '日期': '10月1日', '生产量': 1300, '良品数量': 1200, '良品率': 0.92, '不良品数量': 100, '不良品率': 0.08 },
          { '日期': '10月2日', '生产量': 1350, '良品数量': 1300, '良品率': 0.96, '不良品数量': 50, '不良品率': 0.04 }
        ]
      },
      chartSettings: {
        metrics: ['生产量', '良品数量', '不良品数量', '良品率', '不良品率'],
        // stack: { '生产量': ['良品数量', '不良品数量'] },
        showLine: ['良品率', '不良品率'],
        axisSite: { right: ['良品率', '不良品率'] },
        yAxisType: ['KMB', 'percent'],
        yAxisName: ['数值', '比率'],
        label: {
          normal: { show: true, position: 'top' }
        }
      }
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'mainArticleItemVerticalPadding'
    ]),
    heightValue: function () {
      return (this.mainHeight / 2) - (this.mainArticleVerticalPadding) - (this.mainArticleItemVerticalPadding) + 10 + 'px'
    }
  },
  methods: {
  },
  mounted: function () {
    // this.$nextTick(
    //   () => {
    //     this.$refs.chart.resize()
    //   }
    // )
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.card {
  overflow: hidden;
}
</style>


