<template>
  <div class="carousel carousel-publicity">
    <el-carousel :interval="intervalTime" :arrow="arrowType" :height="heightValue">
      <el-carousel-item v-for="(value, key, index) of AllWorkShopTableData" :key="index">
        <el-card class="card" :style="{height: heightValue}" :body-style="{ padding: '0px' }">
          <div slot="header" class="card-header" v-text="value.workShopName"></div>
          <el-table :data="value.workShopProductTableData" style="width: 100%">
            <el-table-column v-for="(value, key, index) of value.labelAndProp" :prop="value" :label="key" :key="index"></el-table-column>
          </el-table>
        </el-card>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'publicityScreen',
  data () {
    return {
      intervalTime: 8000,
      arrowType: 'never',
      AllWorkShopTableData: {
        positivePoleStir: {
          workShopName: '整体生产情况',
          labelAndProp: {
            '日期': 'date',
            '生产量': 'puroductAmount',
            '良品数': 'goodProductsAmount',
            '良品率': 'goodProductsAmountRatio',
            '不良品数': 'defectiveProductAmount',
            '不良率': 'defectiveProductAratio'
          },
          workShopProductTableData: [
            {
              date: '2017-10-20',
              puroductAmount: 1300,
              goodProductsAmount: 1200,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-19',
              puroductAmount: 1100,
              goodProductsAmount: 1000,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-18',
              puroductAmount: 1200,
              goodProductsAmount: 1100,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-17',
              puroductAmount: 1500,
              goodProductsAmount: 1400,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-16',
              puroductAmount: 1600,
              goodProductsAmount: 1500,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-15',
              puroductAmount: 900,
              goodProductsAmount: 800,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            },
            {
              date: '2017-10-14',
              puroductAmount: 800,
              goodProductsAmount: 700,
              goodProductsAmountRatio: '90%',
              defectiveProductAmount: 100,
              defectiveProductAratio: '10%'
            }
          ]
        }
      }
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'mainArticleItemVerticalPadding'
    ]),
    heightValue: function () {
      return (this.mainHeight / 2) - (this.mainArticleVerticalPadding) - (this.mainArticleItemVerticalPadding) + 10 + 'px'
    }
  },
  methods: {
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.carousel-publicity {
  flex-basis: 50%;
  overflow: hidden;
  padding: $padding-large-vertical $padding-large-horizontal;
  background-clip: content-box;
}
.card {
  .card-header {
    font-size: $font-size-medium;
  }
}
</style>


