<template>
  <div id="publicityScreen">
    <el-row :gutter="20">
      <el-col :span="12">
        <mes-positive-production></mes-positive-production>
      </el-col>
      <el-col :span="12">
        <mes-positive-environment></mes-positive-environment>
      </el-col>
    </el-row>
    <el-row :gutter="20" style="margin-top:20px">
      <el-col :span="12">
        <mes-positive-reject></mes-positive-reject>
      </el-col>
      <el-col :span="12">
        <mes-positive-stage></mes-positive-stage>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import positiveEnvironment from './MainArticleDataScreenPositiveEnvironment'
import positiveProduction from './MainArticleDataScreenPositiveProduction'
import positiveReject from './MainArticleDataScreenPositiveReject'
import positiveStage from './MainArticleDataScreenPositiveStage'
export default {
  name: 'publicityScreen',
  components: {
    'mes-positive-environment': positiveEnvironment,
    'mes-positive-production': positiveProduction,
    'mes-positive-reject': positiveReject,
    'mes-positive-stage': positiveStage
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
#publicityScreen {
  flex-basis: 100%;
  flex-grow: 1;
}
</style>


