<template>
  <div class="setting setting-data-screen">
    <el-tabs type="border-card" :style="{height: heightValue}">
      <el-tab-pane label="轮播图">
        <mes-setting-picture></mes-setting-picture>
      </el-tab-pane>
      <el-tab-pane label="视频">
        <mes-setting-video></mes-setting-video>
      </el-tab-pane>
      <!-- <el-tab-pane label="车间">角色管理</el-tab-pane>
      <el-tab-pane label="成品">定时任务补偿</el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
import videoPane from './MainArticleSettingPublicityScreenVideo'
import picturePane from './MainArticleSettingPublicityScreenPicture'
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'settingDataScreen',
  components: {
    'mes-setting-video': videoPane,
    'mes-setting-picture': picturePane
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding'
    ]),
    ...mapGetters([
      'address'
    ]),
    heightValue: function () {
      return (this.mainHeight) - (this.mainArticleVerticalPadding) + 'px'
    }
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.setting-data-screen {
  flex-grow: 1;
  /* .uploader-video {
  } */

  /* .server-video {
  } */
}
</style>


