<template>
  <div class="chart-box">
    <el-carousel :interval="5000" :height="heightValue">
      <el-carousel-item>
        <el-card class="card" :style="{height: heightValue}">
          <div slot="header" class="card-header small">
            <div class="card-header-title small">
              <span>环境检测 - 温度</span>
            </div>
          </div>
          <div class="card-body">
            <ve-histogram :height="'380px'" class="chart" ref="chart" :data="temperatureData" :settings="temperatureSettings"></ve-histogram>
          </div>
        </el-card>
      </el-carousel-item>
      <el-carousel-item>
        <el-card class="card" :style="{height: heightValue}">
          <div slot="header" class="card-header small">
            <div class="card-header-title small">
              <span>环境检测 - 湿度</span>
            </div>
          </div>
          <div class="card-body">
            <ve-histogram :height="'380px'" class="chart" ref="chart" :data="humidityData" :settings="humiditySettings"></ve-histogram>
          </div>
        </el-card>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'positiveEnvironment',
  data () {
    return {
      temperatureData: {
        columns: ['时间', '温度'],
        rows: [
          { '时间': '8:00', '温度': 25 },
          { '时间': '8:30', '温度': 27 },
          { '时间': '9:00', '温度': 25.5 },
          { '时间': '9:30', '温度': 24 },
          { '时间': '10:00', '温度': 24.4 },
          { '时间': '10:30', '温度': 25.5 },
          { '时间': '11:00', '温度': 24.9 },
          { '时间': '11:30', '温度': 25.6 }
        ]
      },
      temperatureSettings: {
        metrics: ['温度'],
        // stack: { '生产量': ['良品数量', '不良品数量'] },
        showLine: ['温度'],
        label: {
          normal: { show: true, position: 'top' }
        }
      },
      humidityData: {
        columns: ['时间', '湿度'],
        rows: [
          { '时间': '8:00', '湿度': 38 },
          { '时间': '8:30', '湿度': 38.8 },
          { '时间': '9:00', '湿度': 36.5 },
          { '时间': '9:30', '湿度': 38.7 },
          { '时间': '10:00', '湿度': 39 },
          { '时间': '10:30', '湿度': 37.5 },
          { '时间': '11:00', '湿度': 38.6 },
          { '时间': '11:30', '湿度': 37.9 }
        ]
      },
      humiditySettings: {
        metrics: ['湿度'],
        // stack: { '生产量': ['良品数量', '不良品数量'] },
        showLine: ['湿度'],
        label: {
          normal: { show: true, position: 'top' }
        }
      }
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'mainArticleItemVerticalPadding'
    ]),
    heightValue: function () {
      return (this.mainHeight / 2) - (this.mainArticleVerticalPadding) - (this.mainArticleItemVerticalPadding) + 10 + 'px'
    }
  },
  methods: {
  },
  mounted () {
    this.$nextTick(
      () => {
        this.$refs.chart.echarts.resize()
      }
    )
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.chart-box {
  .card {
    overflow: hidden;
    .card-body {
      width: 100%;
    }
  }
}
</style>


