<template>
  <div class="video publicity-screen-video">
    <video :height="heightValue" :controls="isControls" :width="widthValue" :autoplay="isAutoplay" :src="videoSrc" :loop="isLoop">

    </video>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'publicityScreen',
  data () {
    return {
      widthValue: '100%',
      isLoop: 'loop',
      isControls: 'controls',
      isAutoplay: 'autoplay',
      videoSrc: 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4',
      videoRequestParam: 'queryUsedBillboardVedioProgram.do'
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'mainArticleItemVerticalPadding'
    ]),
    ...mapGetters([
      'address'
    ]),
    heightValue: function () {
      return (this.mainHeight / 2) - (this.mainArticleVerticalPadding) - (this.mainArticleItemVerticalPadding) + 10 + 'px'
    },
    videoRequestUrl () {
      return this.address + this.videoRequestParam
    }
  },
  methods: {
    getVideoUrl () {
      fetch(this.videoRequestUrl, {
        method: 'POST'
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then(
              (data) => {
                const videoList = data.map.vedioNameList
                const videoPlayPort = videoList[0].billboard_vedio_url
                this.videoSrc = this.address + videoPlayPort
              }
            )
          }
        }
      )
    }
  },
  mounted () {
    this.getVideoUrl()
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
.publicity-screen-video {
  flex-basis: 50%;
  overflow: hidden;
  padding: $padding-large-vertical $padding-large-horizontal;
  background: $black;
  background-clip: content-box;
}
</style>


