<template>
  <div id="index" :style="{height: windowHeight + 'px'}">
    <mes-main></mes-main>
    <mes-footer id="footer"></mes-footer>
  </div>
</template>

<script>
import main from './Main.vue'
import footer from './Footer.vue'
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'Main',
  components: {
    'mes-main': main,
    'mes-footer': footer
  },
  computed: {
    ...mapState([
      'windowHeight',
      'mainHeight'
    ])
  },
  mounted: function () {
    this.saveWindowSize()
    window.addEventListener('resize', this._.debounce(this.saveWindowSize, 200))
  },
  methods: {
    // 设置容器尺寸
    saveWindowSize () {
      const footer = document.getElementById('footer')
      const footerStyle = window.getComputedStyle(footer)
      const footerHeight = footerStyle.getPropertyValue('height')
      const windowHeight = document.documentElement.clientHeight // 获取窗口高度
      const mainHeight = windowHeight - parseFloat(footerHeight)
      this.SAVE_WINDOW_HEIGHT({height: windowHeight})
      this.SAVE_MAIN_HEIGHT({
        height: mainHeight
      })
    },
    ...mapMutations([
      'SAVE_WINDOW_HEIGHT',
      'SAVE_MAIN_HEIGHT'
    ])
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
#index {
  display:flex;
  flex-direction: column;
  justify-content: space-between;
}
</style>


