import Vue from 'vue'
import Router from 'vue-router'

import app from '../App.vue'
import index from '../components/Index.vue'
import dataScreen from '../components/MainArticleDataScreen.vue'
import dataScreenPositive from '../components/MainArticleDataScreenPositive.vue'
import publicityScreen from '../components/MainArticlePublicityScreen .vue'
import setting from '../components/MainArticleSetting.vue'
import settingPublicityScreen from '../components/MainArticleSettingPublicityScreen.vue'
import settingDataScreen from '../components/MainArticleSettingDataScreen.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    component: app,
    children: [
      {
        path: '',
        redirect: '/index'
      },
      {
        path: '/index',
        component: index,
        children: [
          {
            path: 'dataScreen',
            component: dataScreen,
            children: [
              {
                path: 'positive',
                component: dataScreenPositive
              }
            ]
          },
          {
            path: 'publicityScreen',
            component: publicityScreen
          },
          {
            path: 'setting',
            component: setting,
            children: [
              {
                path: 'dataScreen',
                component: settingDataScreen
              },
              {
                path: 'publicityScrren',
                component: settingPublicityScreen
              }
            ]
          }
        ]
      }
    ]
  }]
})



