<template>
  <el-row :gutter="20">
    <el-col :span="12">
      <div class="uploader-video">
        <el-card class="card" :style="{height: tabsContentValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title">
              <span>上传视频</span>
            </div>
            <div class="card-header-button">
              <el-button type="success" @click="startUploadVideo" :loading="uploaSdate">开始上传</el-button>
            </div>
          </div>
          <div class="card-body">
            <el-form label-position="top" label-width="80px">
              <el-form-item>
                <el-upload
                  :http-request="startUploadVideo"
                  ref="uploadVideo"
                  :on-change="uploadAmount"
                  :action="videoUploadUrl"
                  list-type="picture"
                  accept="video/mp4,video/webm,video/ogg"
                  :multiple="false"
                  :auto-upload="false">
                  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                  <div slot="tip" >只能上传video/mp4, video/webm, video/ogg文件</div>
                </el-upload>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </div>
    </el-col>
    <el-col :span="12">
      <div class="server-video">
        <el-card class="card" :style="{height: tabsContentValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title">
              <span>选择视频</span>
            </div>
            <div class="card-header-button">

            </div>
          </div>
          <div class="card-body">
            <el-table style="width: 100%" :data="videoServerList" v-loading="videoServerListLoadingState" element-loading-text="拼命加载中">
              <el-table-column :min-width="80" prop="serialNumber" label="序号"></el-table-column>
              <el-table-column :min-width="150" prop="uploadTime" label="上传日期"></el-table-column>
              <el-table-column :min-width="150" prop="name" label="名称"></el-table-column>
              <el-table-column :min-width="80" prop="format" label="格式"></el-table-column>
              <el-table-column :min-width="80" prop="size" label="大小"></el-table-column>
              <el-table-column :min-width="200" prop="edit" label="编辑">
                <template slot-scope="scope">
                  <el-button type="success" size="small" @click="videoSelectUse(scope.row)">使用</el-button>
                  <el-button type="danger" size="small" @click="videoDelete(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              class="card-body-pagination"
              @current-change="getVideoPageList"
              :current-page.sync="videoServerListPagination.currentPage"
              :layout="videoServerListPagination.layout"
              :total="videoServerListPagination.total">
            </el-pagination>
          </div>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'settingDataScreen',
  data () {
    return {
      videoServerListParam: 'selectVedioInfo.do',
      videoSelectUseParam: 'insertBillboardVedioProgram.do',
      videoUploadParam: 'uploadBillboardVedio.do',
      videoDeleteParam: 'deleteBillboardVedio.do',
      videoServerListLoadingState: false,
      videoSelectUseLoadingState: false,
      videoServerList: [],
      videoServerListPagination: {
        total: 0,
        currentPage: 1,
        layout: 'total, prev, pager, next'
      },
      videoUploadConfig: {

      },
      videodeleteDataConfig: {

      },
      files: {

      },
      uploaSdate: false
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'elTabsHeight',
      'elTabsHeightVerticalPadding'
    ]),
    ...mapGetters([
      'address'
    ]),
    videoServerListUrl () {
      return this.address + this.videoServerListParam
    },
    videoSelectUseUrl () {
      return this.address + this.videoSelectUseParam
    },
    videoUploadUrl () {
      return this.address + this.videoUploadParam
    },
    videoDeleteUrl () {
      return this.address + this.videoDeleteParam
    },
    heightValue () {
      return (this.mainHeight) - (this.mainArticleVerticalPadding) + 'px'
    },
    tabsContentValue () {
      return parseFloat(this.heightValue) - this.elTabsHeight - this.elTabsHeightVerticalPadding + 'px'
    }
  },
  methods: {
    uploadAmount (file, fileList) {
      this.files = fileList
    },
    startUploadVideo () {
      let form = new FormData()
      for (let value of this.files) {
        form.append('file', value.raw)
      }
      this.uploaSdate = true
      this.$message({
        message: '正在上传文件, 请勿关闭窗口',
        type: 'warning'
      })
      fetch(this.videoUploadUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then(
              (data) => {
                if (data.status === 0) {
                  this.uploaSdate = false
                  this.$message({
                    message: '文件上传成功!',
                    type: 'success'
                  })
                  this.$refs.uploadVideo.clearFiles()
                  this.getVideoPageList()
                }
              }
            )
          }
        },
        () => {
          this.uploaSdate = false
          this.$message({
            message: '文件上传失败, 请重新操作!',
            type: 'warning'
          })
        }
      )
    },
    getVideoPageList ({
      headNum = this.videoServerListPagination.currentPage
    } = {}
    ) {
      let from = new FormData()
      from.append('headNum', headNum)
      fetch(this.videoServerListUrl, {
        method: 'POST',
        body: from
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              console.log(data, 123)
              const dataList = data.map.bbvList
              const counts = data.map.counts
              this.videoServerListPagination.total = counts
              const tempArray = []
              const addVideoDataList = () => {
                for (let [index, value] of dataList.entries()) {
                  const rowData = {
                    serialNumber: '',
                    uploadTime: '',
                    name: '',
                    format: '',
                    size: '',
                    id: '',
                    url: ''
                  }
                  rowData.serialNumber = index + 1
                  rowData.uploadTime = this.$moment(value.billboard_vedio_save_time).format('YYYY-MM-DD')
                  rowData.name = value.billboard_vedio_name
                  rowData.format = value.billboard_vedio_format
                  rowData.size = value.billboard_vedio_size
                  rowData.url = value.billboard_vedio_url
                  rowData.id = value.billboard_vedio_id
                  tempArray.push(rowData)
                }
                this.videoServerList = tempArray
              }
              addVideoDataList()
            })
          }
          else {
            this.$message({
              message: '请检查网络!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '请检查网络!',
            type: 'warning'
          })
        }
      )
    },
    videoSelectUse (row) {
      const form = new FormData()
      form.append('vedioIds', row.id)
      fetch(this.videoSelectUseUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              this.$message({
                message: '提交成功!',
                type: 'success'
              })
            })
          }
          else {
            this.$message({
              message: '提交失败, 请重新操作!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '提交失败, 请重新操作!',
            type: 'warning'
          })
        }
      )
    },
    videoDelete (row) {
      const form = new FormData()
      form.append('vedioId', row.id)
      form.append('vedioUrl', row.url)
      fetch(this.videoDeleteUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              this.$message({
                message: '删除成功!',
                type: 'success'
              })
              this.getVideoPageList()
            })
          }
          else {
            this.$message({
              message: '删除失败, 请重新操作!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '删除失败, 请重新操作!',
            type: 'warning'
          })
        }
      )
    }
  },
  mounted () {
    this.getVideoPageList()
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
</style>


