<template>
  <div id="mainSidebar">

  <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse" :router="isRouter">
    <el-menu-item index="/index/publicityScreen">
      <i class="el-icon-menu"></i>
      <span slot="title">宣传看板</span>
    </el-menu-item>
    <el-submenu index="2">
      <template slot="title">
        <i class="el-icon-picture"></i>
        <span slot="title">生产看板</span>
      </template>
      <el-menu-item-group>
        <span slot="title">极片</span>
        <el-menu-item index="/index/dataScreen/positive">正极看板</el-menu-item>
        <!-- <el-menu-item index="/index/dataScreen/negative">负极看板</el-menu-item> -->
      </el-menu-item-group>
      <!-- <el-menu-item-group>
        <span slot="title">装配</span>
        <el-menu-item index="/index/dataScreen/assemble">装配看板</el-menu-item>
      </el-menu-item-group>
      <el-menu-item-group>
        <span slot="title">化成</span>
        <el-menu-item index="/index/dataScreen/charge">化成看板</el-menu-item>
      </el-menu-item-group> -->
    </el-submenu>
    <el-submenu index="/index/setting">
      <template slot="title">
        <i class="el-icon-setting"></i>
        <span slot="title">系统设置</span>
      </template>
        <!-- <el-menu-item index="/index/setting/dataScreen">数据看板</el-menu-item> -->
        <el-menu-item index="/index/setting/publicityScrren">宣传看板</el-menu-item>
    </el-submenu>
  </el-menu>

  </div>
</template>

<script>
export default {
  name: 'MainSidebar',
  data () {
    return {
      isCollapse: true,
      isRouter: true
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/style/scss/_common.scss";
#mainSidebar {
  background: #eef1f6;
}
</style>


