<template>
  <div id="footer">
    <div class="footer-title">
      <!-- <img src="../assets/img/logo/changchengqiche.png" alt=""> -->
    </div>
    <div class="footer-info">
      <!-- <div class="content">
        <div class="details">
          <span v-for="(item, key, index) of info" :key="index" v-text="item.text" :style="{color: item.color}"></span>
        </div>
      </div> -->
    </div>
    <div class="footer-time">
      <span v-text="time"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Footer',
  data () {
    return {
      title: '长城汽车徐水内外饰事业部',
      time: 'YYYY-MM-DD hh:mm:ss',
      info: [
        {
          color: '#20a0ff',
          text: '通知：热烈欢迎总部领导莅临指导工作！'
        },
        {
          color: '#d9534f',
          text: '紧急通知：请各位员工严格遵守操作规范进行生产!'
        }
      ]
    }
  },
  methods: {
    setNowTime () {
      const settime = () => {
        const nowTime = this.$moment().format('YYYY-MM-DD hh:mm:ss')
        this.time = nowTime
      }
      setTimeout(() => {
        settime()
        this.setNowTime()
      }, 1000)
    },
    rollFooterInfo () {
      const infoBar = document.querySelector('#footer .footer-info')
      const infoBarDetails = infoBar.querySelector('.content .details')
      const infoBarStyle = window.getComputedStyle(infoBar)
      const infoBarDetailsStyle = window.getComputedStyle(infoBarDetails)
      const infoBarStyleWidth = parseInt(infoBarStyle.getPropertyValue('width'))
      const infoBarDetailsWidth = parseInt(infoBarDetailsStyle.getPropertyValue('width'))
      let infoBarLeftValue = infoBarStyleWidth
      infoBarDetails.style.left = infoBarStyleWidth + 'px'
      function startPaly () {
        infoBarLeftValue += -1
        infoBarDetails.style.left = infoBarLeftValue + 'px'
        if (infoBarLeftValue > -infoBarDetailsWidth) {
          window.requestAnimationFrame(startPaly)
        }
        else {
          infoBarLeftValue = infoBarStyleWidth
          window.requestAnimationFrame(startPaly)
        }
      }
      startPaly()
    }
  },
  mounted () {
    this.$nextTick(function () {
      // this.rollFooterInfo()
      this.setNowTime()
    })
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
#footer {
  padding: $padding-large-vertical $padding-large-horizontal;
  min-height: 64px;
  /* background: $gray; */
  display: flex;
  align-items: center;

  .footer-title {
    flex-grow: 1;
    flex-basis: 20%;
    font-size: $font-size-extra-large;
    color: $black;
    background-repeat: no-repeat;
    background-origin: content-box;
    background-position: top left;
  }

  .footer-info {
    flex-grow: 1;
    flex-basis: 60%;
    margin: 0 $padding-large-horizontal;
    font-size: $font-size-extra-large;
    color: $brand-success;
    overflow: hidden;

    .content {
      display: flex;
      flex-wrap: nowrap;
      width: 100%;

      .details {
        white-space: nowrap;
        position: relative;

        span {
          padding-right: $padding-large-horizontal;
        }
      }
    }
  }

  .footer-time {
    display: flex;
    flex-direction: row-reverse;
    flex-grow: 1;
    flex-basis: 20%;
    font-size: $font-size-large;
    font-weight: bold;
    color: $black;
  }
}
</style>


