<template>
  <div id="main">
    <mes-main-sidebar></mes-main-sidebar>
    <mes-main-article></mes-main-article>
  </div>
</template>

<script>
import mainArticle from './MainArticle.vue'
import mainSidebar from './MainSidebar.vue'
export default {
  name: 'main',
  components: {
    'mes-main-article': mainArticle,
    'mes-main-sidebar': mainSidebar
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
#main {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}

.mes-main-sidebar {
  width: 100px;
}
</style>


