<template>
  <div id="mainArticle">
    <router-view></router-view>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'MainArticle',
  computed: {
    ...mapState([
      'mainArticleVerticalPadding'
    ])
  },
  methods: {
    ...mapMutations([
      'SAVE_MAIN_ARTICLE_VERTICAL_PADDING'
    ]),
    saveMainArticleVerticalPadding () {
      const mainArticle = document.getElementById('mainArticle')
      const mainArticleStyle = window.getComputedStyle(mainArticle)
      const mainArticlePaddingTop = mainArticleStyle.getPropertyValue('padding-top')
      const mainArticleVerticalPadding = parseFloat(mainArticlePaddingTop) * 2
      this.SAVE_MAIN_ARTICLE_VERTICAL_PADDING({ verticalPadding: mainArticleVerticalPadding })
    }
  },
  mounted: function () {
    this.saveMainArticleVerticalPadding()
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
#mainArticle {
  display: flex;
  flex-grow: 1;
  /* background: $gray; */
  background-clip: content-box;
  padding: $padding-large-vertical $padding-large-horizontal;
}
</style>


