<template>
  <el-row :gutter="20">
    <el-col :span="12">
      <div class="uploader-picture">
        <el-card class="card" :style="{height: tabsContentValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title">
              <span>上传图片</span>
            </div>
            <div class="card-header-button">
              <el-button type="success" @click="startUploadPicture" :loading="uploaSdate">开始上传</el-button>
            </div>
          </div>
          <div class="card-body">
            <el-form label-position="top" label-width="80px">
              <el-form-item>
                <el-upload
                  :http-request="startUploadPicture"
                  :on-change="uploadAmount"
                  ref="pictureUpload"
                  :action="pictureUploadUrl"
                  list-type="picture"
                  accept="image/jpeg,image/jpg,image/npg"
                  :multiple="true"
                  :auto-upload="false">
                  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                  <div slot="tip" >只能上传jpeg, jpg, npg的图片文件, 图片名称不能超过10个字, 推荐尺寸880px * 480px</div>
                </el-upload>
              </el-form-item>
            </el-form>
          </div>
        </el-card>
      </div>
    </el-col>
    <el-col :span="12">
      <div class="select-picture">
        <el-card class="card" :style="{height: tabsContentValue}">
          <div slot="header" class="card-header">
            <div class="card-header-title">
              <span>选择图片</span>
            </div>
            <div class="card-header-button">
              <el-button type="success" @click="submitSelectPicture(multipleSelection)">播放选中</el-button>
              <el-button type="danger" @click="pictureDelete(multipleSelection)">删除选中</el-button>
            </div>
          </div>
          <div class="card-body">
            <el-table
              style="width: 100%"
              @selection-change="handleSelectionChange"
              :data="pictureServerList"
              v-loading="pictureServerListLoadingState"
              ref="multipleTable"
              element-loading-text="拼命加载中">
              <el-table-column type="selection"> </el-table-column>
              <el-table-column :min-width="80" prop="serialNumber" label="序号"></el-table-column>
              <el-table-column :min-width="120" prop="uploadTime" label="上传日期"></el-table-column>
              <el-table-column :min-width="150" prop="name" label="名称"></el-table-column>
              <el-table-column :min-width="80" prop="format" label="格式"></el-table-column>
              <el-table-column :min-width="80" prop="size" label="大小"></el-table-column>
              <el-table-column :min-width="200" prop="edit" label="编辑">
                <template slot-scope="scope">
                  <el-button type="danger" size="small" @click="pictureDelete(scope.row, scope.$index)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              class="card-body-pagination"
              @current-change="getPicturePageList"
              :current-page.sync="pictureServerListPagination.currentPage"
              :layout="pictureServerListPagination.layout"
              :total="pictureServerListPagination.total">
            </el-pagination>
          </div>
        </el-card>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  name: 'settingDataScreen',
  data () {
    return {
      getpictureListParam: 'queryBillboardPic.do',
      pictureUseParam: 'insertBillboardPicProgram.do',
      pictureUploadParam: 'upLoadBillboardPic.do',
      pictureDeleteParam: 'deleteBillboardPics.do',
      pictureServerListLoadingState: false,
      pictureSelectUseLoadingState: false,
      pictureServerList: [],
      pictureServerListPagination: {
        total: 0,
        currentPage: 1,
        layout: 'total, prev, pager, next'
      },
      multipleSelection: [],
      pictureUploadConfig: {

      },
      picturedeleteDataConfig: {

      },
      files: {},
      uploaSdate: false
    }
  },
  computed: {
    ...mapState([
      'mainHeight',
      'mainArticleVerticalPadding',
      'elTabsHeight',
      'elTabsHeightVerticalPadding'
    ]),
    ...mapGetters([
      'address'
    ]),
    getpictureListUrl () {
      return this.address + this.getpictureListParam
    },
    pictureUseUrl () {
      return this.address + this.pictureUseParam
    },
    pictureUploadUrl () {
      return this.address + this.pictureUploadParam
    },
    pictureDeleteUrl () {
      return this.address + this.pictureDeleteParam
    },
    heightValue () {
      return (this.mainHeight) - (this.mainArticleVerticalPadding) + 'px'
    },
    tabsContentValue () {
      return parseFloat(this.heightValue) - this.elTabsHeight - this.elTabsHeightVerticalPadding + 'px'
    }
  },
  methods: {
    uploadAmount (file, fileList) {
      this.files = fileList
    },
    startUploadPicture () {
      let form = new FormData()
      for (let value of this.files) {
        form.append('file', value.raw)
      }
      this.uploaSdate = true
      this.$message({
        message: '正在上传文件, 请勿关闭窗口',
        type: 'warning'
      })
      fetch(this.pictureUploadUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then(
              (data) => {
                if (data.status === 0) {
                  this.uploaSdate = false
                  this.$message({
                    message: '文件上传成功!',
                    type: 'success'
                  })
                  this.$refs.pictureUpload.clearFiles()
                  this.getPicturePageList()
                }
                else {
                  this.uploaSdate = false
                  this.$message({
                    message: '上传失败请重新操作!',
                    type: 'warning'
                  })
                  this.$refs.pictureUpload.clearFiles()
                }
              }
            )
          }
        },
        () => {
          this.uploaSdate = false
          this.$message({
            message: '上传失败请重新操作!',
            type: 'warning'
          })
          this.$refs.pictureUpload.clearFiles()
        }
      )
    },
    getPicturePageList ({
      Keyword = '',
      PicId = '',
      headNum = this.pictureServerListPagination.currentPage
    } = {}
    ) {
      let from = new FormData()
      from.append('Keyword', Keyword)
      from.append('PicId', PicId)
      from.append('headNum', headNum)
      fetch(this.getpictureListUrl, {
        method: 'POST',
        body: from
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              const dataList = data.map.bbplist
              const counts = data.map.counts
              this.pictureServerListPagination.total = counts
              const tempArray = []
              const addpictureDataList = () => {
                for (let [index, value] of dataList.entries()) {
                  const rowData = {
                    serialNumber: '',
                    uploadTime: '',
                    name: '',
                    format: '',
                    size: '',
                    id: '',
                    url: ''
                  }
                  rowData.serialNumber = index + 1
                  rowData.uploadTime = this.$moment(value.img_save_time).format('YYYY-MM-DD')
                  rowData.name = value.img_name
                  rowData.format = value.img_format
                  rowData.size = value.img_size
                  rowData.url = value.img_url
                  rowData.id = value.img_id
                  tempArray.push(rowData)
                }
                this.pictureServerList = tempArray
              }
              addpictureDataList()
            })
          }
          else {
            this.$message({
              message: '请检查网络!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '请检查网络!',
            type: 'warning'
          })
        }
      )
    },
    handleSelectionChange (value, row) {
      this.multipleSelection = value
    },
    submitSelectPicture (rows) {
      const idList = []
      for (let iterator of rows) {
        const id = iterator.id
        idList.push(id)
      }
      const form = new FormData()
      form.append('picIds', idList)
      fetch(this.pictureUseUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              this.$message({
                message: '提交成功!',
                type: 'success'
              })
            })
          }
          else {
            this.$message({
              message: '提交失败!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '提交失败!',
            type: 'warning'
          })
        }
      )
    },
    pictureDelete (rows, index) {
      let idStringJoint = ''
      if (rows instanceof Array) {
        let tempArray = []
        for (let iterator of rows) {
          let id = iterator.id
          tempArray.push(id)
        }
        let tempSrting = tempArray.join(',')
        idStringJoint = tempSrting
      }
      else {
        idStringJoint = rows.id
      }
      const form = new FormData()
      form.append('ids', idStringJoint)
      fetch(this.pictureDeleteUrl, {
        method: 'POST',
        body: form
      }).then(
        (response) => {
          if (response.ok) {
            response.json().then((data) => {
              this.$message({
                message: '删除成功!',
                type: 'success'
              })
              this.getPicturePageList()
            })
          }
          else {
            this.$message({
              message: '删除失败, 请重新操作!',
              type: 'warning'
            })
          }
        },
        () => {
          this.$message({
            message: '删除失败, 请重新操作!!',
            type: 'success'
          })
        }
      ).catch()
    }
    // submitUpload () {
    //   this.$refs.pictureUpload.submit()
    // }
  },
  mounted () {
    this.getPicturePageList()
  }
}
</script>

<style lang = "scss" scoped>
@import "../assets/style/scss/_common.scss";
</style>


